.title{
    font-size: 23px;
    font-weight: 700;
}

.daily-item{
    background-color: #f5f5f5;
    border-radius: 15px;
    height: 40px;
    margin: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    padding: 5px 20px;
}

.small-icon{
    width: 40px;
}

.day{
    color: #212121;
    flex: 1 1; 
    /* flex 1 1 to make the day class like space-between example we have 2 text1 text2 we want it to have spaced available because 1 means this can be grow or bigger so we want a space evenly to it or space between */
    font-weight:600px;
    margin-left: 15px;
}

.descriptions {
    flex: 1 1;
    margin-right: 15px;
    text-align: right;
}

.min-max{
    color: #757575;
}

.daily-details-grid {
    grid-row-gap:0;
    row-gap:0;
    /* gap between row 0  and the column 15px */
    grid-column-gap: 15px;
    column-gap: 15px;
    display: grid;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
}

.daily-details-grid-item {
    display: flex;
    height: 30px;
    align-items: center;
    justify-content: space-between;
}

.daily-details-grid-item label:first-child {
    color: #757575;
}

.daily-details-grid-item label:last-child {
    color: #212121;
}